.projects-slider-div {
	height: 40%;
	width: 80%;
}

.project-div {
	height: 280px;
	width: 230px;
	border-radius: 10px;
	background-color: #D9D9D9;
	padding: 20px 20px 20px 20px;
	margin-left: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.project-img {
	height: 40%;
	width: 80%;
}

.project-label {
    height: 60%;
    width: 100%;
    line-height: normal;
    /*text-align: left;*/
}

.project-btn {
    border: none;
    background-color: transparent;
    font-weight: bold;
}
.project-div a {
    background-color: orange;
    height: 50px;

}
.github-img {
    height: 30px;
    width: 30px;
}