/* Common breakpoints:
0 - 600px
601px, 768px, 992px, 1200px

can also do (orientation: landspace) for phone

*/



/* from 0 to 600px use this styling. This will be our phone styling. */
@media (max-width: 768px) {
	.App {
		/*overflow-x: hidden;*/
		overflow: auto;
		overflow-x: hidden;
		padding: none;
	}

	.scrollBarNav {
		position: fixed;
		
		height: 50px;
		width: 100%;
		top: 0px;
		left: 0px;
		padding-top: 3px;
		background-color: #D9D9D9;
		
		/*border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;*/
	}

	ul {
		list-style-type: none;
		cursor: pointer;
		display:flex;
		justify-content: space-around;
	}
	.contact-form {
		width: 65%;
	}

}
@media (min-width: 769px) {

	.scrollBarNav {
		position: fixed;
		
		/*height: 60%;*/
		right: 0px;
		margin-top: 172px;
		margin-bottom: 30%;
		background-color: #D9D9D9;
		
		border-top-left-radius: 15px;
		border-bottom-left-radius: 0px;
	}
	ul {
		height: 100%;
		line-height: 135px;
		list-style-type: none;
		margin-right: 25px;
		cursor: pointer;
	}
	.contact-form {
		width: 45%;
	}

}
@media (min-width: 1300px) {
	.contact-form {
		width: 35%;
	}
}


