/* width */
::-webkit-scrollbar {
	width: 20px;
	height: 50px;
	
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
	
	margin-top: 180px;
	margin-bottom: 180px;
	margin-left: 10px;
	margin-right: 50px;
	height: 20px;
	background: #A0A0A0;
	border-top-right-radius: 15px;
	border-bottom-right-radius: 0px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
	background: white;
	border-radius: 10px;
	
  }

::-webkit-scrollbar-track-piece:end {
    background: transparent;
    margin-bottom: 40px; 
}

::-webkit-scrollbar-track-piece:start {
    background: transparent;
    margin-top: 40px;
}

